.contacts {
    position: absolute;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    // width: 110vw;
    // height: 110vh;
    // display: flex;
    // align-items: center;
    // justify-content: center;
    // flex-direction: column;
    // padding: calc(var(--index) * 5.4) calc(var(--index) * 1.5) calc(var(--index) * 1.5) calc(var(--index) * 1.5);
    // background: rgba(89, 81, 71, .25);
    // background: url('../../../public/img/contacts/bg.jpg');
    // background-size: 100% auto;

    // &::before {
    //     position: absolute;
    //     width: 100%;
    //     height: 100%;
    //     content: '';
    //     top: 0;
    //     left: 0;
    //     background: rgba(38, 27, 17, .15);
    //     border-radius: 10px;
    // }

    &__wrapper {
        // width: 86vw;
        // height: 80vh;
        // border-radius: 10px;
        overflow: hidden;
        width: 110%;
        height: 110%;
        position: relative;
        padding: calc(var(--index) * 5) calc(var(--index) * 19.5);
        display: flex;
        align-items: center;
        justify-content: flex-end;
        // flex-direction: column;
        background: var(--contactsBg);
        background-size: 110% auto;
        transition: var(--transitionContacts);
        will-change: transform;

        &::before {
            position: absolute;
            width: 100%;
            height: 100%;
            content: '';
            top: 0;
            left: 0;
            background: var(--contactsBgBefore);
            border-radius: 10px;
        }
    }

    &__content {
        width: 110%;
        height: 110%;
        position: relative;
        padding: calc(var(--index) * 2);
        display: flex;
        align-items: center;
        justify-content: flex-end;
        // flex-direction: column;
        background: url('../../../public/img/contacts/bg.jpg');
        background-size: 100% auto;

        &::before {
            position: absolute;
            width: 100%;
            height: 100%;
            content: '';
            top: 0;
            left: 0;
            background: rgba(38, 27, 17, .15);
            border-radius: 10px;
        }
    }

    &__card {
        position: relative;
        width: calc(var(--index) * 18);
        height: calc(var(--index) * 10);
        padding: calc(var(--index) * .5);
        margin-top: calc(var(--index) * 2);
        border-radius: 2px;
        background: url('../../../public/img/contacts/card.jpg');
        background-size: cover;
        box-shadow:
            //top shadow
            0px -10px 10px var(--contactsBoxShadowTop),
            //bottom shadow
            0px 10px 20px var(--contactsBoxShadow),
            //left shadow
            -10px 0px 20px var(--contactsBoxShadow),
            //right shadow
            10px 0px 20px var(--contactsBoxShadow),
            //inner shadow
            inset 1px 1px 20px var(--contactsBoxShadowInset);
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        // cursor: pointer;
        transition: .4s;
        will-change: transform;

        &_logo {
            // --contactsLogoScale: calc(var(--index) * 100);
            width: calc(var(--index) * 16);
            height: calc(var(--index) * 6);
            // border: 2px solid red;
            // z-index: 10;
            // zoom: calc(var(--index)*100);
            // transform: scale(.75) translateX(-10%);
            background: url('../../../public/img/contacts/texture2.jpg');
            background-size: cover;
            mask: url('../../../public/img/logo-w.png');
            // mask-repeat: no-repeat;
            -webkit-mask: url('../../../public/img/logo-w.png');
            -webkit-mask-size: cover;
            mask-size: cover;
            -webkit-mask-position: center;
            mask-position: center;
        }

        &_text {
            font-size: calc(var(--index) * 1.25);
            // color: rgba(242, 242, 242, .9);
            color: transparent;
            background: url('../../../public/img/contacts/texture5.jpg');
            background-size: cover;
            background-clip: text;
            -webkit-background-clip: text;
            text-shadow: -1px -1px 1px rgba(38, 27, 17, .1);
            letter-spacing: -1px;
            word-spacing: 0px;
            padding-top: calc(var(--index) * .9);
            // font-family: 'Philosopher', sans-serif;
            font-family: 'Yeseva One', cursive;
            text-align: center;
            line-height: calc(var(--index) * 1.25);
        }

        &.front {
            transform: rotateZ(-7deg);
            // left: -15%;
        }

        &.back {
            position: absolute;
            left: calc(var(--index) * 10);
            top: calc(var(--index) * 9);
            color: transparent;
            text-shadow: 1px 1px 1px rgba(38, 27, 17, .2);
            transform: rotateZ(4deg);
            justify-content: space-between;
        }

        &_info {
            font-size: calc(var(--index) * .9);
            font-weight: 500;
            word-spacing: 8px;
            background: url('../../../public/img/contacts/texture5.jpg');
            background-clip: text;
            -webkit-background-clip: text;
            background-size: cover;
            // height: calc(var(--index) * 1);
            line-height: 30px;
        }

        &_phone {
            font-size: calc(var(--index) * 1.2);
            background: url('../../../public/img/contacts/texture5.jpg');
            background-clip: text;
            -webkit-background-clip: text;
            background-size: cover;
            height: calc(var(--index) * 1.54);
            font-family: 'Philosopher', sans-serif;
            letter-spacing: 2px;
            // font-weight: 500;
        }

        &_right {
            position: absolute;
            width: calc(var(--index) * 15);
            right: calc(var(--index) * 1);
            bottom: calc(var(--index) * 1);
            text-align: center;
            text-shadow: 1px 1px 1px rgba(38, 27, 17, .2);
        }

        &_left {
            margin-bottom: calc(var(--index) * 2);
        }

        @media(any-hover:hover) {
            &:hover {
                transform: scale(1.07);
            }
        }
    }

    &__map {
        &_button {
            position: absolute;
            left: calc(var(--index) * 3.7);
            bottom: calc(var(--index) * 6.9);
            width: calc(var(--index) * 5.4);
            height: calc(var(--index) * 5.4);
            padding: calc(var(--index) * .5);
            // margin-top: calc(var(--index) * 2);
            border-radius: 50%;
            background: url('../../../public/img/contacts/card.jpg');
            background-size: cover;
            box-shadow:
                //top shadow
                0px -10px 10px var(--contactsBoxShadowTop),
                //bottom shadow
                0px 10px 20px var(--contactsBoxShadow),
                //left shadow
                -10px 0px 20px var(--contactsBoxShadow),
                //right shadow
                10px 0px 20px var(--contactsBoxShadow),
                //inner shadow
                inset 1px 1px 20px var(--contactsBoxShadowInset);
            display: flex;
            align-items: center;
            justify-content: center;
            // flex-direction: column;
            cursor: pointer;
            transition: .4s;
            will-change: transform;
            transform: rotateZ(34deg);

            & p {
                font-size: calc(var(--index) * 1.5);
                background: url('../../../public/img/contacts/texture5.jpg');
                background-clip: text;
                -webkit-background-clip: text;
                background-size: cover;
                // height: calc(var(--index) * 1.54);
                // font-family: 'Philosopher', sans-serif;
                font-weight: 500;
                letter-spacing: 2px;
                color: transparent;
                // font-family: 'Yeseva One', cursive;
                transition: .4s;
                text-shadow: 1px 1px 1px rgba(38, 27, 17, .2);
                // text-decoration: underline;
            }

            @media(any-hover:hover) {
                &:hover {
                    transform: scale(1.17) rotateZ(0deg);
                    transition: .4s;

                    & p {
                        // color: transparent;
                        background: url('../../../public/img/contacts/texture7.jpg');
                        background-clip: text;
                        -webkit-background-clip: text;
                        background-size: cover;
                        transition: .4s;
                    }
                }
            }
        }
    }

    &__catalog {
        &_button {
            position: absolute;
            left: calc(var(--index) * 11.4);
            bottom: calc(var(--index) * 6.4);
            width: calc(var(--index) * 6.9);
            height: calc(var(--index) * 3);
            padding: calc(var(--index) * .5) calc(var(--index) * 1);
            // margin-top: calc(var(--index) * 2);
            // border-radius: 50%;
            background: url('../../../public/img/contacts/card.jpg');
            background-size: cover;
            box-shadow:
                //top shadow
                0px -10px 10px var(--contactsBoxShadowTop),
                //bottom shadow
                0px 10px 20px var(--contactsBoxShadow),
                //left shadow
                -10px 0px 20px var(--contactsBoxShadow),
                //right shadow
                10px 0px 20px var(--contactsBoxShadow),
                //inner shadow
                inset 1px 1px 20px var(--contactsBoxShadowInset);
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            transition: .4s;
            will-change: transform;
            transform: rotateZ(-39deg);
            border-radius: 13px;

            & p {
                font-size: calc(var(--index) * 1.25);
                background: url('../../../public/img/contacts/texture5.jpg');
                background-clip: text;
                -webkit-background-clip: text;
                background-size: cover;
                // height: calc(var(--index) * 1.54);
                // font-family: 'Philosopher', sans-serif;
                font-weight: 500;
                letter-spacing: 2px;
                color: transparent;
                // font-family: 'Yeseva One', cursive;
                transition: .4s;
                text-shadow: 1px 1px 1px rgba(38, 27, 17, .2);
                // text-decoration: underline;
            }

            @media(any-hover:hover) {
                &:hover {
                    transform: scale(1.17) rotateZ(0deg);
                    transition: .4s;

                    & p {
                        // color: transparent;
                        background: url('../../../public/img/contacts/texture9.jpg');
                        background-clip: text;
                        -webkit-background-clip: text;
                        background-size: cover;
                        transition: .4s;
                    }
                }
            }
        }
    }

    &__light {
        width: 110%;
        height: 110%;
        position: absolute;
        right: 0;
        top: 0;
        border-radius: 50%;
        background: radial-gradient(circle, rgba(242, 242, 242, .5) 20%, rgba(242, 242, 242, 0) 60%);
        opacity: .2;
        z-index: 9;
        pointer-events: none;
        // border: 2px solid red;
        transition: var(--transitionContacts);
        will-change: transform;
    }
}