.page {
    position: fixed;
    height: 100vh;
    width: 100%;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;

    &__hi {
        z-index: 15;
        position: fixed;
        width: calc(var(--index) * 26);
        // height: 25%;
        // top: 0;
        // left: 0;
        // padding: calc(var(--index) * 7.5) 0px 0px calc(var(--index) * 5.4);
        top: 0;
        left: 0;
        padding: calc(var(--index) * 8) 0px 0px calc(var(--index) * 4);
        transition: var(--transition);
        transform: translate3d(calc(var(--scrollTop) * -1.15), 0, 0);
        will-change: transform;

        &_content {
            color: #f2f2f2;
            font-size: calc(var(--index) * 2.4);
            padding: 20px;
            background: var(--hiBG);
            box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
            backdrop-filter: blur(5.4px);
            -webkit-backdrop-filter: blur(5.4px);
            transition: .4s;
        }

        &_text {
            // padding: 0px 0px 40px 0px;
            // font-weight: 500;
            line-height: 1;
            letter-spacing: -2.7px;
            color: var(--hiText);
            // text-align: center;
            // font-weight: var(--hiTextWeight);
            // font-weight: 600;
            font-family: 'Yeseva One', cursive;
            transition: .4s;
            margin-bottom: calc(var(--index) * 1.25);
        }

        &_subtext {
            // padding: 0px 0px 30px 0px;
            font-size: calc(var(--index) * .85);
            line-height: 1.4;
            letter-spacing: 2px;
            color: var(--hiText);
            // text-align: center;
            // font-weight: var(--hiSubTextWeight);
            // font-weight: 500;
            font-family: 'Philosopher', sans-serif;
            // font-style: italic;
            transition: .4s;
            margin-bottom: calc(var(--index) * 1.25);
        }

        &_button {
            font-size: calc(var(--index) * 1);
            // letter-spacing: -1px;
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 24px 48px;
            transition: .4s;
            font-weight: 500;
            margin: 0 auto;
            width: 84%;
            color: rgba(242, 242, 242, .9);
            // background-color: #261B11;
            position: relative;
            // font-family: 'Philosopher', sans-serif;
            // font-style: italic;

            &::before {
                position: absolute;
                width: 100%;
                height: 100%;
                content: '';
                top: 0;
                left: 0;
                background-color: #BFB79F;
                transform: scale(0);
                opacity: 0;
                transition: .4s;
                z-index: -1;
            }

            &::after {
                position: absolute;
                width: 100%;
                height: 100%;
                content: '';
                top: 0;
                left: 0;
                background-color: #3A4010;
                transform: scale(1);
                opacity: 1;
                transition: .4s;
                z-index: -1;
            }

            // &::after {
            //     position: absolute;
            //     width: 100%;
            //     height: 100%;
            //     content: '';
            //     top: 0;
            //     left: 0;
            //     background-color: #261B11;
            //     transition: .4s;
            //     // z-index: -1;
            // }

            @media (any-hover: hover) {
                &:hover {
                    color: #3A4010;
                    transition: .2s;
                    // transform: scale(1.02);
                    // background-color: inherit;

                    &::before {
                        transform: scale(1);
                        transition: .2s;
                        opacity: 1;
                    }

                    &::after {
                        transform: scale(0);
                        transition: .2s;
                        opacity: 0;
                    }
                }
            }

            &:active {
                transition: .2s;
                transform: scale(.98);
            }
        }
    }

    &__scroll {
        position: absolute;
        z-index: 15;
        font-size: calc(var(--index) * 1);
        color: var(--scrollText);
        transition: var(--transition);
        transform: translate3d(0, calc(var(--scrollTop) * -8), 0);
        will-change: transform;

        @media(any-hover:hover) {
            & :hover {
                transform: scale(1.05);
            }
        }

        &_button {
            letter-spacing: 4px;
            transition: .4s;
            will-change: transform;
            font-family: 'Philosopher', sans-serif;
            font-style: italic;

            & span {
                animation: scroll 2.5s infinite linear;
                font-size: 42px;
            }
        }

        &_anim {
            position: absolute;
            top: calc(var(--index) * 1.3);
            left: calc(var(--index) * 17.4);
            font-size: 34px;
            z-index: 15;
            width: 45px;
            height: 45px;
            border-radius: 50%;
            background: rgba(191, 183, 158, 1);
            // transition: .4s;
            // padding: 3.5px 0px 0px 3.5px;
            display: flex;
            align-items: center;
            justify-content: center;

            // @media(any-hover:hover) {
            //     &:hover {
            //         transition: .2s;
            //         color: rgba(242, 242, 242, .9);
            //         background: rgba(58, 63, 15, 1);
            //         // transform: scale(1.07);
            //     }
            // }



            & svg {
                transform: rotateZ(calc(var(--scrollTopRotate) * .4deg));
                transition: var(--transition);
                will-change: transform;
                transform-origin: center;
                // color: #3A4010;
            }

        }
    }
}

.header {
    height: 200vh;
    overflow: hidden;
    max-width: 100%;
}

.layer {
    &__left {
        height: 100%;
        width: 50%;
        transform: translate3d(calc(-1 * var(--scrollTop)), 0, 0);
        will-change: transform;
        transition: var(--transition);
        z-index: 10;

        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            // background: rgba(89, 81, 71, .1);
            background: var(--layerBeforeBackground);
            transition: .4s;
        }
    }

    &__right {
        height: 100%;
        width: 50%;
        transform: translate3d(var(--scrollTop), 0, 0);
        will-change: transform;
        transition: var(--transition);
        z-index: 10;

        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            // background: rgba(89, 81, 71, .1);
            background: var(--layerBeforeBackground);
            transition: .4s;
        }
    }
}

@keyframes scroll {
    0% {
        opacity: .1;
    }

    50% {
        opacity: 1;
    }

    100% {
        opacity: .1;
    }
}