.page__lang {
    position: absolute;
    top: calc(var(--index) * 1.5);
    left: calc(var(--index) * 9.5);
    z-index: 15;
    display: flex;
    cursor: pointer;

    &::before {
        position: absolute;
        top: 0;
        left: 0;
        height: 34px;
        width: 69px;
        background: var(--langButtonBG);
        border-radius: 5px;
        content: '';
        transition: .4s;
    }

    &::after {
        position: absolute;
        top: -6px;
        left: -8px;
        content: '';
        width: 45px;
        height: 45px;
        border-radius: 50%;
        background: rgba(191, 183, 158, 1);
        transition: .4s;
    }

    & div {
        padding: 45px 0px 0px 0px;
        // font-size: calc(var(--index) * .8);
        font-size: 22px;
        // letter-spacing: 1px;
        margin-left: -5px;
        text-transform: uppercase;
        font-weight: 500;
        color: var(--langButtonText);
        transition: .4s;

        &:first-child {
            margin-right: 17px;
        }
    }

    &.active {
        &::after {
            transform: translateX(38px);
        }
    }

    @media(any-hover:hover) {
        &:hover {
            // &::before {
            //     transition: .2s;
            //     background: rgba(191, 183, 158, 1);
            // }

            &::after {
                transition: .2s;
                background: rgba(58, 63, 15, 1);
            }
        }
    }
}