.tomain__button {
    position: absolute;
    width: 64px;
    height: 64px;
    // bottom: 40px;
    // right: 40px;
    bottom: calc(var(--index) * 1);
    right: calc(var(--index) * 1);
    cursor: pointer;
    z-index: 15;
    color: var(--mainPageScrollModeColor);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 40px;
    transition: .4s;

    // transition: var(--transition);
    // transform: translate3d(calc(var(--scrollTop) * .25), 0, 0);
    // transform: translate3d(0, calc(var(--scrollTop) * .5), 0);
    // will-change: transform;

    & svg {
        // transition: .4s;
        transform: rotateZ(90deg);
    }

    &::before {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        border: 3px solid var(--mainPageScrollModeColor);
        content: '';
        transition: .4s;
    }

    @media(any-hover:hover) {
        &:hover {
            color: var(--mainPageScrollModeColorReverse);
            transform: scale(1.1);
            transition: .2s;
            background: var(--mainPageScrollModeColor);

            &.tomain__button::before {
                transition: .2s;
                transform: rotateZ(-180deg);
            }

            // & svg {
            //     transition: .2s;
            // }
        }
    }
}