.page-about__button {
    z-index: 10;
    position: fixed;
    // width: calc(var(--index) * 22.5);
    // max-width: calc(var(--index) * 17.5);
    // height: 25%;
    // bottom: 0;
    // left: 0;
    // padding: 0px 0px calc(var(--index) * 5) calc(var(--index) * 13.5);
    bottom: 0;
    right: 0;
    padding: 0px calc(var(--index) * 7) calc(var(--index) * 10.5) 0px;
    transition: var(--transition);
    transform: translate3d(calc(var(--scrollTop) * .8), 0, 0);
    will-change: transform;

    &-lil {
        padding: 0px calc(var(--index) * 7) calc(var(--index) * 6) 0px;
        transform: translate3d(calc(var(--scrollTop) * .9), 0, 0);
    }

    &_content {
        color: var(--pageAboutButtonContentColor);
        font-size: calc(var(--index) * 2.5);
        padding: 15px;
        background: var(--pageAboutButtonContentBg);
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(5.4px);
        -webkit-backdrop-filter: blur(5.4px);
        width: min-content;
        text-transform: uppercase;
        cursor: pointer;
        transition: .4s;
        // font-weight: 500;
        font-family: 'Yeseva One', cursive;
        letter-spacing: calc(var(--index) * -.15);
        position: relative;
        line-height: calc(var(--index) * 2.4);

        &-lil {
            font-size: calc(var(--index) * 1.5);
            line-height: calc(var(--index) * 1.4);
            letter-spacing: calc(var(--index) * .02);
            text-transform: none;
            font-family: 'Philosopher', sans-serif;
            font-weight: 700;
            // font-style: italic;
            width: calc(var(--index) * 17.5);
            border: 4px solid #a30808;
            cursor: default;

            & p {
                font-size: calc(var(--index) * .85);
                line-height: calc(var(--index) * 1);
                margin-top: calc(var(--index) * .5);
                font-family: 'Montserrat', sans-serif;
                font-weight: 400;
            }
        }

        // &::before {
        //     position: absolute;
        //     width: 100%;
        //     height: 100%;
        //     top: 0;
        //     left: 0;
        //     content: '';
        //     background: #3A4010;
        //     // background: rgba(38, 27, 17, .4);
        //     // box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        //     // backdrop-filter: blur(5.4px);
        //     // -webkit-backdrop-filter: blur(5.4px);
        //     transition: .25s;
        //     opacity: 0;
        //     z-index: -1;
        //     transform: scale(0);
        // }

        // @media(any-hover:hover) {
        // &:hover {
        // color: var(--pageAboutButtonContentHover);
        // transition: .2s;
        // background: none;

        // &::before {
        // opacity: 1;
        // transform: scale(1);
        // transition: .2s;
        // }
        // }
        // }
    }
}