.page__menu {
    z-index: 20;
    position: fixed;
    width: 64px;
    height: 64px;
    // top: 40px;
    // right: 40px;
    top: calc(var(--index) * 1);
    right: calc(var(--index) * 1);
    border: 3px solid var(--navModeColor);
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    transition: .4s;
    will-change: transform;

    &::before {
        content: '';
        width: 28px;
        height: 3px;
        background: var(--navModeColor);
        margin: 0px 0px 10px 0px;
        transition: .2s;
    }

    &::after {
        content: '';
        width: 28px;
        height: 3px;
        background: var(--navModeColor);
        transition: .2s;
    }

    @media (any-hover: hover) {
        &:hover {
            background: var(--navModeColor);
            transition: .2s;
            transform: scale(1.1) rotateZ(-180deg);

            &::before {
                background: var(--navModeColorReverse);
            }

            &::after {
                background: var(--navModeColorReverse);
            }
        }
    }

    &.active {
        &::before {
            transform: rotateZ(-45deg) translate3d(-4.5px, 4.5px, 0);
        }

        &::after {
            transform: rotateZ(45deg) translate3d(-4.5px, -4.5px, 0);
        }
    }

    &_list {
        position: fixed;
        top: calc(var(--index) * .5);
        right: calc(var(--index) * .5);
        background: rgba(38, 27, 17, .4);
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(5.4px);
        -webkit-backdrop-filter: blur(5.4px);
        z-index: 15;
        padding: calc(var(--index) * 3) calc(var(--index) * 3) calc(var(--index) * 1.5) calc(var(--index) * 1.5);
        transform: translateX(250%);
        transition: transform .5s ease 0s;

        &.active {
            transform: translateX(0%);
        }
    }

    &_item {
        font-size: calc(var(--index) * 1.2);
        margin-bottom: calc(var(--index) * 1);
        letter-spacing: 2px;
        // color: #F2F2F2;
        cursor: pointer;
        transition: .4s;
        will-change: transform;
        color: rgba(242, 242, 242, 1);

        // &:last-child {
        //     margin-bottom: 0px;
        // }

        &-link {
            &:last-child {
                & li {
                    margin-bottom: 0px;
                }
            }
        }



        &.active {
            // color: var(--navModeColor);
            // color: rgba(242, 242, 242, .8);
            // cursor: default;
            // text-decoration: underline;
            color: var(--navModeColor);
            cursor: default;
            text-decoration: underline;



            @media (any-hover: hover) {
                &:hover {
                    transform: scale(1);
                }
            }
        }

        @media (any-hover: hover) {
            &:hover {
                transition: .2s;
                transform: scale(1.1);
            }
        }
    }
}