:root {
    --transition: transform 1.25s cubic-bezier(.075, .5, 0, 1);
    --transitionContacts: .75s cubic-bezier(.075, .5, 0, 1);
    --index: calc(1vw + 1vh);
    --transitionCatalog: 1.25s cubic-bezier(.2, .5, 0, 1);
    --transitionCatalogImage: .25s cubic-bezier(.2, .5, 0, 1);
    // --transitionCatalogCursor: 1.25s cubic-bezier(.05, .5, 0, 1);
}

