.contacts__button {
    z-index: 10;
    position: fixed;
    // width: calc(var(--index) * 22.5);
    // max-width: calc(var(--index) * 17.5);
    // height: 25%;
    // bottom: 0;
    // right: 0;
    // padding: 0px calc(var(--index) * 3.5) calc(var(--index) * 9) 0px;
    bottom: 0;
    left: 0;
    padding: 0px 0px calc(var(--index) * 3.5) calc(var(--index) * 1.75);
    transition: var(--transition);
    transform: translate3d(calc(var(--scrollTop) * -.85), 0, 0);
    will-change: transform;

    &_content {
        color: var(--pageContactsButtonContentColor);
        font-size: calc(var(--index) * 2.15);
        padding: 15px;
        background: var(--pageContactsButtonContentBg);
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(5.4px);
        -webkit-backdrop-filter: blur(5.4px);
        // width: min-content;
        text-transform: uppercase;
        cursor: pointer;
        transition: .4s;
        // font-weight: 500;
        font-family: 'Yeseva One', cursive;
        letter-spacing: calc(var(--index) * -.15);
        position: relative;

        // line-height: calc(var(--index) * 3.2);
        // & a {
        //     color: var(--pageContactsButtonContentColor);
        //     transition: .4s;
        // }

        &::before {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            content: '';
            background: #3A4010;
            // background: rgba(38, 27, 17, .4);
            // box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
            // backdrop-filter: blur(5.4px);
            // -webkit-backdrop-filter: blur(5.4px);
            transition: .25s;
            opacity: 0;
            z-index: -1;
            transform: scale(0);
        }

        @media(any-hover:hover) {
            &:hover {
                color: var(--pageContactsButtonContentHover);
                transition: .2s;
                background: none;

                &::before {
                    opacity: 1;
                    transform: scale(1);
                    transition: .2s;
                }

                // & a {
                //     color: var(--pageContactsButtonContentHover);
                //     transition: .2s;
                // }
            }
        }
    }
}